body {
  overflow-x: hidden; /* Prevent scroll on narrow devices */
  padding-top: 0px!important;
  padding-bottom: 0px!important;
  background: #ffffff; /*url(/assets/img/Op-tec%20website%20watermark%202.svg);*/
  background-size: cover;
  background-attachment: fixed;
  color: #ffffff;
}

.container {
    padding-right: 0px;
    padding-left: 0px;
}

.row {
    margin-right: 0px;
    margin-left: 0px;
}

.padded {
    padding-right: 15px;
    padding-left: 15px;    
}

a, a:focus, a:hover {
  color: #116297; /* change link colour to 'Op-tec blue' */
  text-decoration: none /* remove underline from links */
}

.btn-link {
  color: #116297; /* change link colour to 'Op-tec blue' */
  text-decoration: none!important; /* remove underline from links */
}

.btn-outline-primary:not(:disabled):not(.disabled).active {
    background-color: #116297;
    border-color: #116297;
}

.big {font-size: larger}

.card {
    border-radius: 0rem;
    margin-bottom: 30px;
}
.accordian-card {
    border-radius: 0rem;
    margin-bottom: 0px;
    background-color: #ffffff;
}
.accordian-header {
    background-color: #ffffff;
}
.modal-content {
    border-radius: 0rem;
}

.dropdown-menu {
    border-radius: 0rem;
    border: 0px;
}

.space-text {
    line-height: 1.3;
}

/* highlight current page in navbar */
.current {
    color: black!important;
}

/* text colour = black */
.black {
    color: #202020;
}

.blue {
    color: #116297;
}

/* text colour = white */
.white {
    color: #ffffff!important;
}
/* background colour = white */
.white-bg {
    background-color: #ffffff!important;
}
/* change the background color */
.bg-custom {
    background-color: #ffffff!important; /* Preventing bootstrap.css making grey */
}

.bg-dark {
    background-color: #086298!important;
}

.custom-primary {
    color: #116297;
    border-color: #116297;
}

.custom-primary:focus, .custom-primary:hover{
  color: #ffffff;
  background-color: #116297;
  border-color: #116297;
}

.disabled {border-color: rgba(100,100,100,.7);
            color: rgba(100,100,100,.7)!important;
}

.fa-info-circle {
    color: #116297;
}

.tooltip.show {opacity: 0.7;}
.tooltip > .tooltip-inner {border-radius: 0px;}

@media (max-width: 991.98px) {
  .offcanvas-collapse {
    position: fixed;
    top: 74px; /* Height of navbar */
    bottom: 0;
    left: 100%;
    width: 100%;
    padding-right: 1rem;
    padding-left: 1rem;
    overflow-y: auto;
    visibility: hidden;
    background-color: #ffffff;
    transition-timing-function: ease-in-out;
    transition-duration: .3s;
    transition-property: left, visibility;
    z-index: 2;
  }
  .offcanvas-collapse.open {
    left: 0;
    visibility: visible;
  }
}

.nav {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: nowrap;
  flex-wrap: nowrap;
  padding-bottom: 1rem;
  /*margin-top: -1px;*/
  overflow-x: auto;
  color: rgba(255, 255, 255, .75);
  text-align: center;
  white-space: nowrap;

}

.card-text {line-height: 1.1;}

.text-white-50 { color: rgba(255, 255, 255, .5); }

.lh-100 { line-height: 1; }
.lh-125 { line-height: 1.25; }
.lh-150 { line-height: 1.5; }

#mapid {
    position: relative;
    padding-bottom: 75%; // This is the aspect ratio
    height: 0;
    overflow: hidden;
    margin-bottom: 20px;
}
#mapid {
    
    top: 0;
    left: 0;
    width: 100% !important;
    height: 100% !important;
}

.box-custom {
    border-radius: 0px;
}

.card-img-top {
    margin-bottom: 0px;
}

.speechbubble {
      -moz-border-radius: 0px;
      -webkit-border-radius: 0px;
      border-radius: 0px;
      padding: 15px; 
    }

.top-margin {
    margin-top: 16px;
}

.badge {
    border-radius: 0px;
}

.jumbotron {
    background-color: transparent;
}

.bannerContent {
    z-index: -1;
    position: relative;
}

.business-card-title {
    margin-bottom: 0;
}

.bottom-align-text {
    position: absolute;
    bottom: 0;
   
    padding-left: 30px;
  }

.tab { text-indent:10px }

.right-indent {
    padding-right: 40px;
}

.modal-close {
    color: #000000 !important;
}

.video-container {
    position: relative;
    padding-bottom: 56.25%;
    padding-top: 30px;
    height: 0;
    overflow: hidden;
}

.video-container iframe,  
.video-container object,  
.video-container embed {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.carousel-item {background-size: cover;}